* {
  padding: 0;
  margin: 0;
  border: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  font-family: inherit;
}

a {
  text-decoration: none;
}

button {
  background: none;
  border: none;
  padding: 0;
}

li {
  list-style-type: none;
}

.visability {
  display: none;
}

.header-container {
  width: auto;
  height: 88px;
}

.header-navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 20px;
}
.header-navigation .divider {
  width: 2px;
  height: 32px;
  border-radius: 1px;
  background: #eaeaea;
}

@media screen and (min-width: 768px) {
  .header-navigation__mobile-divider--visability {
    display: none;
  }
  .header-navigation__main-logo {
    margin-right: 33%;
  }
  .header-navigation .visability {
    display: block;
  }
}
@media screen and (min-width: 1440px) {
  .header-container {
    display: none;
  }
}
.aside-menu--active {
  display: block;
  transform: translateX(0);
}

.aside-menu-container {
  position: fixed;
  overflow: scroll;
  display: none;
  flex-direction: column;
  top: 0px;
  left: 0px;
  width: 320px;
  height: 100vh;
  z-index: 10;
  background-color: #fff;
  box-shadow: 12px 0px 40px 0px rgba(14, 24, 80, 0.5);
}

.aside-menu {
  display: flex;
  flex-direction: column;
  padding: 18px 24px 32px 24px;
  height: 95%;
  justify-content: space-between;
}
.aside-menu a {
  color: #1b1c21;
  text-decoration: none;
}
.aside-menu__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 46px;
}
.aside-menu__logo {
  margin-left: -40px;
}
.aside-menu__footer-nav {
  display: flex;
  margin-bottom: 16px;
}
.aside-menu__item .item__link {
  display: block;
  padding-bottom: 32px;
  align-items: center;
  color: #000;
  transition: color 0.4s ease;
}
.aside-menu__item::before {
  content: "";
  width: 4px;
  height: 32px;
  border-left: 4px solid #41f6d7;
  border-radius: 8px;
  position: absolute;
  left: 0px;
  transform: translateY(-15%) rotate(180deg);
  transition: left 0.6s ease;
  display: none;
}
.aside-menu__item:hover::before {
  display: block;
}
.aside-menu__item:hover {
  font-weight: 600;
}
.aside-menu__footer-item {
  margin-right: 16px;
}
.aside-menu__contact-email {
  display: block;
  margin-bottom: 12px;
}
.aside-menu__contact_phone {
  display: block;
  font-weight: bold;
  font-size: 24px;
  margin-bottom: 40px;
}
.aside-menu__lang {
  display: flex;
}
.aside-menu__lang li:not(:last-child) {
  margin-right: 7px;
}
.aside-menu__lang li a:hover {
  opacity: 0.5;
}
.aside-menu .icon:hover {
  opacity: 0.5;
}

.aside-menu--active {
  display: block;
  transform: translateX(0);
}

.title-container {
  margin-bottom: 24px;
  display: flex;
  margin-top: 24px;
}
.title-container .title-text {
  color: #1b1c21;
  font-family: TTLakes-Bold;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 40px; /* 142.857% */
  letter-spacing: -0.6px;
  margin-left: 1%;
  margin-right: auto;
}

.desktop-navigation {
  display: none;
  align-items: center;
}
.desktop-navigation span {
  color: #1b1c21;
  text-align: right;
  font-family: TTLakesCondensed-Medium;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  padding-top: 10px;
}
.desktop-navigation__repair-btn {
  display: flex;
  align-items: center;
  margin-right: 32px;
}
.desktop-navigation__repair-btn .desktop-navigation__text-repair {
  margin-right: 8px;
}
.desktop-navigation__checkstatus-btn {
  display: flex;
  align-items: center;
  margin-right: 30px;
}
.desktop-navigation__checkstatus-btn .desktop-navigation__text-checkstatus {
  margin-right: 8px;
}

.menu {
  width: 100%;
  display: flex;
  flex-direction: row;
  list-style: none;
  overflow-x: scroll;
  margin-bottom: 12px;
}
.menu__section-element {
  overflow-wrap: normal;
  white-space: nowrap;
  color: #7e7e82;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
  margin-left: 16px;
  border: 2px solid transparent;
  padding-top: 8px;
  padding-right: 9px;
  padding-bottom: 8px;
  padding-left: 17px;
}
.menu__section-element:hover {
  border-radius: 6px;
  border-color: #b8ffec;
  background: #fff;
}

.menu::-webkit-scrollbar {
  width: 0;
}

@media screen and (min-width: 1440px) {
  .desktop-navigation {
    display: flex;
  }
}
.main-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;
}

.content-text {
  height: 90px;
  width: 90%;
  margin-bottom: 16px;
  overflow: hidden;
  color: #1b1c21;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px; /* 128.571% */
  letter-spacing: 0.2px;
  margin-left: 16px;
  margin-right: 16px;
  align-self: center;
}

.more-button {
  display: flex;
  align-items: center;
  margin-left: 16px;
}

.more-button__text {
  color: #1b1c21;
  font-family: TTLakesCondensed-Medium;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  letter-spacing: -0.2px;
}

.content-image {
  width: 100%;
  height: 100%;
  margin: 27px 0;
}

@media screen and (min-width: 768px) {
  .main-content {
    display: flex;
    flex-direction: row;
  }
  .content-image {
    width: 360px;
    height: 208px;
    margin-left: 48px;
    margin-top: 0;
    border-radius: 5%;
  }
  .main-container {
    height: 600px;
  }
  .content-text {
    width: 312px;
    height: 142px;
  }
}
@media screen and (min-width: 1440px) {
  .main-container {
    min-width: 1120px;
    min-height: 535px;
  }
  .content-text {
    width: 490px;
    height: 160px;
  }
  .content-image {
    width: 530px;
    height: 307px;
  }
}
@media screen and (min-width: 768px) {
  .swiper {
    display: none;
  }
}
.brands__title--background {
  flex-shrink: 0;
  background: linear-gradient(270deg, #f8f8f8 0%, #fff 98.61%);
  margin-bottom: 16px;
}

.brands__title {
  color: #7e7e82;
  font-family: TTLakes-Regular;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px; /* 150% */
  letter-spacing: -0.2px;
  text-transform: uppercase;
  padding: 16px;
}

.swiper-card {
  padding: 20px 16px;
  min-width: 224px;
  min-height: 72px;
  max-height: 72px;
  flex-shrink: 0;
  border-radius: 6px;
  border: 1px solid #eaeaea;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.swiper-wrapper {
  margin-bottom: 16px;
  padding-left: 16px;
}

.brands-swiper__swiper-wrapper {
  /* padding: 30px; */
  min-height: 100%;
}

.brands-swiper__swiper-slide {
  text-align: center;
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #dddddd;
}

.swiper-pagination-bullet-active {
  background-color: #b5b6bc;
}

.swiper-pagination {
  position: relative;
}

.brand__grid-block {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  -moz-column-gap: 24px;
  column-gap: 24px;
  row-gap: 16px;
  margin-bottom: 24px;
  display: none;
  padding: 0 24px;
}

.more-button {
  align-items: center;
  margin-left: 16px;
  background: none;
  border: none;
  padding: 0;
  margin-bottom: 34px;
}

.more-button__text {
  color: #1b1c21;
  font-family: TTLakes-Regular;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.2px;
  vertical-align: top;
}

.brand__item:not(:nth-child(-n+8)) {
  display: none;
}

@media screen and (max-width: 1119px) {
  .brand__item:not(:nth-child(-n+6)) {
    display: none;
  }
}
@media screen and (width > 767px) {
  .brand__grid-block {
    display: grid;
  }
}
@media screen and (width > 1119px) {
  .brand__grid-block {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
@media screen and (width < 768px) {
  .more-button {
    display: none;
  }
}
.brand__item--opened {
  display: list-item !important;
}

.hidden {
  display: none;
}

.background-grey {
  background: #f8f8f8;
}

.prices__table-content {
  padding-left: 40px;
}

.prices__table-title {
  display: flex;
}

.table-item__content {
  display: flex;
  justify-content: space-between;
}
.table-item__content .content-item {
  width: 25%;
  padding: 20px;
}